/**
 * 
 */
class StateElement {
    constructor(machine, state, callback) {
        this._machine = machine;
        this._state = state;
        this.callback = callback;
    }
    get machine() {
        return this._machine;
    }
    get state() {
        return this._state;
    }
    execute(iteration) {
        if (this.callback) {
            return this.callback(this, iteration);
        }
    }
}
/**
 * 
 */
class StateMachineCollection {
    constructor(machine) {
        this.machine = machine;
        this._elements = [];
    }
    get elements() {
        return this._elements;
    }
    add(el) {
        var self = this;
        if (el) {
            var toUpdate = [];
            if (!Array.isArray(el)) {
                toUpdate = [el];
            } else {
                toUpdate = el;
            }
            toUpdate.forEach((tu)=>self.elements.push(tu));
        }
        return this;
    }
    clear() {
        this._elements = [];
    }
}

/**
 * 
 */
class EntityElement extends StateElement {
    constructor(machine, state, callback) {
        super(machine, state, callback);
        this.state.children = [];
    }
    get x() {
        return this.state.x;
    }
    get y() {
        return this.state.y;
    }
    get parent() {
        return this.state.parent;
    }
    set parent(p) {
        this.state.parent = p;
    }
    get children() {
        return this.state.children;
    }
}

/**
 * 
 */
class StateRenderer extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            return this.callback(el, iteration);
        }
    }
}

/**
 * 
 */
class StateOperator extends StateElement {
    execute(iteration, el) {
        if (this.callback) {
            return this.callback(el, iteration);
        }
    }
}

/**
 * 
 */
class StateBuilder extends StateElement {

}


/**
 * 
 */
class StateMachine {
    constructor() {
        this.remIterations = 0;
        this.iteration = 1;
        this.maxIterations = 10;

        this.stateElements = new StateMachineCollection(this);
        this.stateOperators = new StateMachineCollection(this);
        this.stateBuilders = new StateMachineCollection(this);
        this.stateRenderers = new StateMachineCollection(this);

        this._postIteration = null;
    }
    get elements() {
        return this.stateElements;
    }
    get operators() {
        return this.stateOperators;
    }
    get builders() {
        return this.stateBuilders;
    }
    get renderers() {
        return this.stateRenderers;
    }
    get postIteration() {
        return this._postIteration;
    }
    set postIteration(x) {
        this._postIteration = x;
    }
    reset() {
        this.elements.clear();
        this.iteration = this.remIterations = 0;
        this.builders.elements.forEach((stateBu) => {
            stateBu.execute(1);
        })
    }
    run(iterations) {
        this.builders.elements.forEach((stateBu) => {
            stateBu.execute(1);
        });
        this.advance(iterations);
    }
    advance(iterations) {
        var self = this;
        var advance = () => {
            this.remIterations = iterations;
            if (!(this.remIterations >= 0 && this.iteration < this.maxIterations)) {
                return;
            }
            var newElements = [];
            this.elements.elements.forEach((stateEl) => {
                var els = [];
                this.operators.elements.forEach((stateOp) => {
                    var r = stateOp.execute(this.iteration, stateEl);
                    if(r) els = els.concat(r);
                });
                this.renderers.elements.forEach((stateRe) => {
                    setTimeout(()=>{
                        stateRe.execute(this.iteration, stateEl);
                    },0);
                });
                els.forEach((ell)=>{
                    setTimeout(()=>{
                        self.renderers.elements.forEach((stateRe) => {
                            stateRe.execute(self.iteration, ell);
                        });
                    },0);
                    newElements.push(ell);
                });
            });
            if(this.postIteration) {
                this.postIteration(this, newElements);
            }
            this.iteration++;
            this.remIterations = (this.remIterations !== -1 ? this.remIterations - 1 : -1);
            setTimeout(advance, 0);
        };
        setTimeout(advance, 0);
    }
}


export { StateElement, StateMachineCollection, StateMachine, EntityElement, StateOperator, StateRenderer, StateBuilder };